  export default {
    data() {
      return {
        curIndex: 0,
        datalist: [
			
			{
            coverImg: require('../../static/image/happy/11.jpg'),
            title: '心灵毒鸡汤',
            content: '你要感谢那些伤害过你的人，正因为这些人你才能够变得强大。',
            jumpUrl: '../jiTang/jiTang'
          }, {
            coverImg: require('../../static/image/happy/22.jpg'),
            title: '网易云乐评',
            content: '范晓萱《氧气》不等了，你来找我，我也不要了。',
            jumpUrl: '../wangyi/wangyi'
          },
          {
            coverImg: require('../../static/image/happy/33.jpg'),
            title: '开心一笑',
            content: '你有多久没有哈哈大笑过了呢。',
            jumpUrl: '../xiaoHua/xiaoHua'
          },
		  {
		    coverImg: require('../../static/image/happy/88.jpg'),
		    title: '最爱大可爱',
		    content: '所有最好的时光，在这里停留，相伴到老不腻。。。',
		    jumpUrl: '../Alisa/Alisa'
		  },
          {
            coverImg: require('../../static/image/happy/99.jpg'),
            title: '每日一文',
            content: '人类是唯一能从无用知识的增长中获取快感的动物。',
            jumpUrl: '../day/day'
          },
          {
            coverImg: require('../../static/image/happy/55.jpg'),
            title: 'Hitokoto一言',
            content: '我们总会看到有那么一两个句子能穿透你的心。',
            jumpUrl: '../say/say'
          },
          {
            coverImg: require('../../static/image/happy/100.jpg'),
            title: '土味情话',
            content: '苦海无边，回头是你。',
            jumpUrl: '../tuWei/tuWei'
          },
        ],
        circular: true, // 设置衔接滑动
        previousMargin: '80rpx',
        nextMargin: '80rpx',
        animationData: {}, // 卡片放大动画
        animationData2: {}, // 卡片缩小动画
        heightChange: 0,
        cardHeight: 0
      }
    },
    components: {},
    onLoad() {
      wx.getSystemInfo({
        success: (res) => {
          this.heightChange = res.windowHeight;
          this.cardHeight = (this.heightChange - 445) / 2;

          console.log('高度', this.heightChange)
        }
      })
    },
    methods: {
      jumpInfo(params) {
		uni.navigateTo({
		    url:params
		});
      },
      handleChange(e) {
        this.curIndex = e.mp.detail.current
        this.changeActive()
        this.changeNormal()
      },
      // 收缩
      changeNormal() {
        var animation2 = wx.createAnimation({
          duration: 500,
          timingFunction: 'ease'
        })
        this.animation2 = animation2
        animation2.scale(0.9).opacity(0.3).step()
        this.animationData2 = animation2.export()
      },
      // 展开
      changeActive() {
        var animation = wx.createAnimation({
          duration: 500,
          timingFunction: 'ease'
        })
        this.animation = animation
        animation.scale(1).opacity(1).step()
        this.animationData = animation.export()
      }
    }
